.header-container {
  background: var(--primary-app-color);

  height: 150px !important;
}
.header-container nav {
  max-width: 1920px;
  margin-inline: auto;
}
.brand-name {
  color: var(--white);
  font-family: "Poppins SemiBold";
  font-size: 2rem;
}
.primary-text h1{
  font-size: 36px;
}

.px-9 {
  padding-inline: 90px;
}
.flex-rows {
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.navbar-option-mobile {
  display: none;
}
@media all and (max-width:1024px){
  .header-container{
    padding-inline: 40px !important;
  }
  .brand-name {
    font-size: 1.7rem;
  }
  .flex-rows{
    align-items: center;
  }
  .nav-items {
    font-size: 1rem;
  }
  .resume-content{
    width: 90% !important;
    height: fit-content !important;
  }
  .education-new{
    width: 70%;
  }
  .about-me-parent,
  .contact-me-card{
    width: 90%;
  }
  .contact-me-card .my-auto{
    margin-inline: auto ;
  }
}


@media all and (max-width: 767px) {
  .header-container {
    height: 60px !important;
    padding: 0 !important;
  }
  .flex-rows {
    flex-direction: column;
    justify-content: start;
  }
  .navbar-new {
    border-right: 1px solid #fff;
    background: #24263b;
    position: fixed;
    height: 100vh;
    width: 60vw;
    z-index: 33;
    transform: translateX(-700px);
  }
  .navbar-new-active {
    border-right: 1px solid #fff;
    background: var(--primary-app-color);
    position: fixed;
    height: 100vh;
    width: 60vw;
    z-index: 33;
  }
  .transition-all {
    transition: all 0.3s ease-in-out;
  }
  .px-9 {
    padding-inline: 0px;
  }
  .nav-items {
    padding-block: 20px;
  }
  .brand-name {
    font-size: 1.4rem;
    padding: 14px 20px;
  }
  .bars {
    height: 4px;
    width: 40px;
    background: #fff;
    float: right;
    z-index: 33;
    /* margin-top: 10px; */
    position: relative;
    top: 27px;
    right: 20px;
  }
  .bars::before {
    display: block;
    position: relative;
    top: 10px;
    content: "";
    color: #fff;
    width: 40px;
    height: 4px;
    background: #fff;
  }
  .bars::after {
    display: block;
    position: relative;
    top: -14px;
    content: "";
    color: #fff;
    width: 40px;
    height: 4px;
    background: #fff;
  }
  .profile-container {
    flex-direction: column;
  }
  .profile-details{
    width: 90%;
  }
  .home-container {
    padding-top: 60px;
    min-height: 780px;
    clip-path: polygon(50% 0%, 100% 0%, 100% 90%, 50% 95%, 0% 90%, 0 0);
  }
  .navbar-option-mobile {
    position: fixed;
    display: block;
    top: 0;
    width: 100vw;
    height: 60px;
  }
  .navbar-option {
    position: fixed;
    display: block;
    width: 100vw;
    z-index: 10000;
  }
  .profile-details-role h1{
    font-size: 25px;
  }
  .section-text-title{
    font-size: 2.7rem;
  }
  .about-me-container{
    margin: 0 !important;
  }
  .about-me-parent {
    width: 80%;
  }
  .about-me-card{
    margin-block: 1rem !important;
  }
  .about-me-lottie lottie-player{
    width: 500px;
    height: min-content;
  }
  .resume-container{
    padding: 0 !important;
  }
  .resume-parent{
    padding-block: 60px;
    clip-path: polygon(0 8%, 100% 0%, 100% 100%, 0 95%);
    max-height: 1030px;
  }
  .resume-content{
    margin-top: 1rem;
    height: fit-content !important;
    width: 80% !important;
  }
  .project-name,
  .university-name{
    font-size: 1rem;
  }
  .project-link,
  .university-degree {
    font-size: 0.7rem;
  }
  .year-passed-out{
    padding-inline: 6px ;
    font-size: 0.7rem;
  }
  .project-new,
  .education-new{
    width: 70%;
  } 
  .language {
    font-size: 1rem;
  }
  .company-name a{
    font-size: 1.2rem;
  }
  .contact-me-card{
    margin-top: 1rem;
    width: 80%;
    height: fit-content;
  }
  .contact-me-card .my-auto{
    padding-bottom: 15px;
  }
  .right-contact-card{
    width: 100%;
  }
  .input-groups {
    padding-right: 0;
  }
  .relative-bg {
    clip-path: polygon(0 7%,100% 11%,100% 94%,0% 100%);
    height: 1200px;
  }
  .contact-me-parent{
    padding-bottom: 0;
  }

}
@media all and (max-width:320px){
  .brand-name {
    font-size: 1.3rem;
  }
  .nav-items{
    font-size: 1.2rem;
  }
  .resume-parent{
    max-height: fit-content;
  }
  .project-new,
  .education-new{
    width: 63% !important;
  }
  .company-name ~ .year-passed-out{
    padding-inline: 4px;
    font-size: 0.5rem;
  }
  .get-in-touch {
    font-size: 1.6rem;
  }
  .section-text-title {
    font-size: 2.3rem;
  }
  .contact-me-lottie lottie-player{
    height: min-content !important;
  }
  .right-contact-card {
    padding: 10px;
  }
}
