.contact-me-container{
    color: #fff;
}
.contact-me-parent{
    padding-block: 118px;
}
.relative-bg{
    clip-path: polygon(0 5%,100% 15%,100% 80%,0 95%);
    background: var(--primary-app-color);
    height: 1000px;
    width: 100%;
    position: absolute;
    z-index: -4;
}
.contact-me-card{
    background: #1c1c1c;
    width: 70%;
    max-width: 1374px;
    margin-inline: auto;
    box-shadow: 10px 10px 63px 4px rgb(243,240,240);
    height: 500px;
    margin-top: 42px;
}
.get-in-touch{
    color: #fff;
    font-family: "Poppins Regular";
    font-weight: 700;
    font-size: 2rem;

}
.input-groups{
    border: none;
    border-radius: 10px;
    padding: 10px 0 10px 10px;
    width: 100%;
}
.right-contact-card{
    background: rgb(0,7,61);
    box-shadow: 10px 3px 33px 4px rgb(243,240,240);
    border-radius: 20px;
    width: 80%;
    padding: 24px;
    margin-inline: auto;
}
.btn-success{
    width: 100%;
}

@media all and (max-width:991px){
    .right-contact-card{
        width: 100%;
    }
}