.university-name{
    font-family: "Poppins Regular";
    color: var( --primary-app-color);
    font-size: 1.4rem;
    font-weight: 800;
}
.university-degree{
    font-family: "Poppins Regular";
    color: #1c1c1c;
    font-size: 0.8rem;
    font-weight: 800;
    opacity: 0.9;
    text-transform: uppercase;
}
.year-passed-out{
    font-family: monospace;
    font-size: 1rem;
    background: #cacaca;
    color: #444;
    padding: 10px 10px;
    border-radius: 20px;
}
.education-new{
    width: 80%;
}