.about-me-parent{
    display: flex;
    flex-direction: column;
    width: 70%;
    margin-inline: auto;
    max-width: 1374px;
}
.about-me-card{
    display: flex;
    justify-content: center;
}

.about-me-some-text{
    font-family: "Poppins Regular";
    font-weight: 600;
    font-size: 1.2rem;
    opacity: 0.8;
    text-align: justify;
}
.few-highlights{
    font-family: "Poppins Regular";
    padding-block: 12px;
}
.few-text{
    font-family: "Poppins Regular";
    font-weight: 400;
    font-size: 1.5rem;
}
.few-list{
    font-family: "Poppins Regular";
    font-size: 1.2rem;
}
.few-list ul{
    padding-inline: 20px;
}
.about-me-lottie lottie-player{
    width: 500px;
    height: 500px;
}